@import url('https://fonts.googleapis.com/css2?family=Poppins:ital@1&display=swap');
.breed-select {
  display: block;
  padding: 10px;
  margin: 10px;
}

ul {
  list-style: none;
  margin: 0;
  padding: 0;
}

ul li {
  padding-top: 15px;
}

h1 {
  margin: 0;
  padding: 0;
}

.cat-info {
  display: flex;
}

.descr-container {
  padding-left: 25px;
}
.three {
  display: block;
  position: relative;
  top: 25%;
  left: 50%;
  transform: translate(-50%, -50%);
}

.back {
  background-color: rgba(250, 235, 215, 0.5);
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  display: flex;
  flex-direction: column;
}
.loader-animation {
  text-align: center;
  margin: 0;
}
.loader {
  font-family: "Poppins";
  text-align: center;
  margin: 0;
}
#loader-4 span {
  display: inline-block;
  width: 20px;
  height: 20px;
  border-radius: 100%;
  background-color: #3498db;
  margin: 35px 5px;
  opacity: 0;
}

#loader-4 span:nth-child(1) {
  animation: opacitychange 1s ease-in-out infinite;
}

#loader-4 span:nth-child(2) {
  animation: opacitychange 1s ease-in-out 0.33s infinite;
}

#loader-4 span:nth-child(3) {
  animation: opacitychange 1s ease-in-out 0.66s infinite;
}

@keyframes opacitychange {
  0%,
  100% {
    opacity: 0;
  }

  60% {
    opacity: 1;
  }
}

.hidden {
  position: absolute;
  top: -100%;
  left: -100%;
  width: 1px;
  height: 1px;
  overflow: hidden;
}